import {Button} from 'primereact/button'
import {SplitButton} from 'primereact/splitbutton';

export default class TestComponents extends React.Component{
    constructor() {
        super();
        this.state = {
            items: [
                {
                    label: 'Update', 
                    icon: 'fa fa-refresh', 
                    command: (e) => {
                        this.growl.show({severity:'success', summary:'Updated', detail:'Data Updated'});
                    }
                },
                {
                    label: 'Delete', 
                    icon: 'fa fa-close',
                    command: (e) => {
                        this.growl.show({ severity: 'success', summary: 'Delete', detail: 'Data Deleted' });
                    }
                },
                {
                    label: 'React Website', 
                    icon: 'fa fa-link',
                    command:(e) => {
                        window.location.href = 'https://facebook.github.io/react/'
                    }
                },
                {   label: 'Upload', 
                    icon: 'fa fa-paint-brush', 
                    command:(e) => {
                        window.location.hash = "/fileupload"
                    }
                }
            ]
        }

        this.save = this.save.bind(this);
    }
    
    save() {
        this.growl.show({severity: 'success', summary: 'Success', detail: 'Data Saved'});
    }

    render(){
        return(
            <div>
                <h1>Hola Mundo soy SvmReact</h1>
                <Button label="Save"/>
                <Button label="Secondary"  className="ui-button-secondary"/>
                <SplitButton label="Success" className="ui-button-success" model={this.state.items} />
            </div>
        )
    }
}


